ul {
  font-family: "Roboto", sans-serif;
  list-style: none;
  font-weight: 700;
  letter-spacing: 1px;
  padding-top: 20px;
  margin: auto;
}

ul li {
  padding-left: 5px;
  color: rgb(255, 255, 255);
}

.deleteButton {
  padding-left: 20px;
  font-family: "Roboto", sans-serif;
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
  background-color: transparent;
  border-style: none;
  border-radius: 20px;
  color: rgb(255, 255, 255);
}
